import React from 'react';
import { Route, Routes } from 'react-router-dom';
import DashboardLayout from '../../layout/Dashboard';

const CardList = React.lazy(() => import('./CardList/Index'));
const SentCardList = React.lazy(() => import('./SentCardList/SentCardList'));

function CardRouter() {
  return (
    <Routes>
      <Route
        path=""
        element={(<DashboardLayout><CardList /></DashboardLayout>)}
      />
      <Route
        path="sentCard"
        element={(<DashboardLayout><SentCardList /></DashboardLayout>)}
      />

    </Routes>
  );
}

export default CardRouter;
